:root{
  --text-primary:#000;
  --bgPrimary:#fff;
  --bgSecondary: #F9F9F9;
  --pink: #0071bd;
  --light-blue: #e6f7ff;
  --border: #d9d9d9;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

iframe{
  display: none;
}

a:hover{
  color: #0071bd !important;
  color: var(--pink) !important;
}

.app{
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.navbar{
  background-color: rgb(0, 21, 41);
}

.main{
  flex: 1 1;
  width: 100%;
}

.routes{
  padding: 20px;
}

.nav-container{
  left: 0;
  margin: 10px;
  margin: 0px;
  background-color: rgb(0, 21, 41);
}

.logo-container{
  background-color: #001529;
  display: flex;
  padding: 20px;
  align-items: center;
  width: 100%;
}

.logo{
  margin:0 0 0 15px;
}

.logo a{
  color: white;
}

.menu-control-container{
  display: none !important;
  position: absolute !important;
  right: 10px !important;
  top: 25px !important;
  font-size: 1.2rem !important;
  background-color: #F9F9F9 !important;
  background-color: var(--bgSecondary) !important;
  border: none !important;
}

.loader{
  height: 81vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width:1300px){
  .main{
